.App {
  text-align: justify;
  background-color: rgb(240, 240, 240);
  width: 60vw;
  height: 100vh;
  margin: auto;
  overflow-y: scroll;
  border: 1px solid;
}

@media screen and (max-width: 768px) {
  nav {
    width: 100vw !important;
  }
  .App {
    width: 100vw;
  }
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
}

button {
  padding: 16px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  background-color: black;
  color: white;
  border-radius: 8px;
  cursor: pointer;
}

h3,
h4 {
  /* margin-bottom: 0; */
  margin: 0px 16px 0px 16px;
}

nav {
  padding-top: 5px;
  border-bottom: 4px solid black;
  text-align: center;
  position: fixed;
  top: 0;
  width: 60vw;
  background: rgb(125, 163, 145);
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
}

nav a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 16px 32px;
}

nav a:hover {
  background: black;
  color: white;
}

#page-body {
  max-width: 700px;
  margin: auto;
  padding-top: 64px;
}

.article-list-item {
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
}

.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid black;
}

#add-comment-form {
  border-bottom: 2px solid black;
  padding: 16px;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}

#add-comment-form input,
textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: 320px;
}

#upvotes-section p {
  display: inline-block;
  margin: 0 16px;
  padding: 0px;
}

.comment-box {
  border: 2px solid olive;
  margin-top: 5px;
}

.comment {
  border-bottom: 2px solid black;
  padding: 16px;
}

.related-articles {
  font-size: 12px;
  text-align: center;
}

.related-articles--section h5 {
  text-decoration: underline;
  margin: 16px;
  text-align: center;
}

.related-articles--section h3 {
  text-align: center;
}

h1 {
  text-align: center;
}

#upvotes-section {
  display: flex;
  height: 50px;
  align-items: center;
}

.upvotes-box {
  margin-top: 5px;
}

.btn-upvote {
  padding: 10px;
  background-color: rgb(125, 163, 145);
  color: #000;
  float: right;
  flex: 2;
}
